import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {
  public isProduction: boolean = true;
  public imgBadgePath: string = '';
  public imgAZ900BadgePath: string = '';

  constructor() { }

  ngOnInit(): void {
    if (this.isProduction) {
      this.imgBadgePath =
        'https://parikshit-salunkhe.com/assets/images/AZ204.png';
        this.imgAZ900BadgePath =
        'https://parikshit-salunkhe.com/assets/images/AZ900.png';       
        // 'http://www.sevenseas-intl.com/assets/parikshitPortfolio/AZ900.png';
    } else {
      this.imgBadgePath = '../../assets/images/AZ204.png';
      this.imgAZ900BadgePath = '../../assets/images/AZ900.png';
    }
  }

}
